* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: none;
}

body {
    margin: 0;
    font-family: "page";
}

@font-face {
    font-family: "page";
    src: url("assets/fonts/K2D-text.ttf");
}

.navbar_cont {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    height: 64px;
    background-color: black;
}

.navbar_cont a {
    max-height: 64px;
    text-decoration: none;
    display: flex;
    color: white;
}

.navbar_link {
    padding: .3rem;
}

.navbar_links {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    z-index: 1;

}

.navbar_logo {
    display: flex;
}

.navbar_burger {
    display: none;
}
.icon64 {
    width: 64px;
    height: 64px;
    cursor: pointer;
}

.t1 {
    color: white;
    font-size: 20px;
}

.ticket {
    display: flex;
    position: absolute;
    right: 64px;
    z-index: 0;
}

.check2 {
    position: fixed;
    left: 320px;
}

@keyframes anim-chat {

    from {
        transform: translateX(-30px);
    }
    to {
        transform: translateX(50px);
    }
}

.anim-cat {
    animation: anim-chat 1.2s linear alternate infinite;
}

@media screen and (max-width: 768px) {
    .show_nav .navbar_links {
        width: 100vw;
        right: 0;
        visibility: visible;
    }

    .hide_nav .navbar_links {
        visibility: hidden;
    }

    .navbar_links {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: fixed;
        width: 0;
        right: 0;
        height: 100vh;
        padding: 2rem;
        background-color: black;
        transition: all .8s ease-out;
    }

    /* BONUS */
    .navbar_item::after {
        content: "";
        display: block;
        margin: 0 auto;
        width: 3vw;
        height: 1px;
        position: relative;
        top: 30%;
        background-color: #FFF;
    }

    .navbar_item:last-child::after {
        display: none;
    }

    .navbar_link {
        display: block;
        padding: 1.5rem;
        font-size: 5vw;
    }

    .burger_bar,
    .burger_bar::before,
    .burger_bar::after {
        display: block;
        width: 40px;
        height: 3px;
        position: relative;
        border-radius: 3px;
        background-color: #fff;
        left: -16px;
        z-index: 2;
        transition: all .5s ease-in-out;
    }

    .burger_bar::before,
    .burger_bar::after {
        content: "";
        position: absolute;
        left: 0;
    }

    .burger_bar::before {
        transform: translateY(-12px);
    }

    .burger_bar::after {
        transform: translateY(12px);
    }

    .lang,
    .loupe {
        display: none;
    }

    .navbar_burger {
        display: contents;
    }

    .navbar_burger:hover {
        cursor: pointer;
    }

    .navbar_links {
        position: fixed;
        top: -5rem;
    }

    .show_nav .burger_bar::before {
        transform: rotate(45deg);
    }

    .show_nav .burger_bar::after {
        transform: rotate(-45deg);
    }

    .show_nav .burger_bar {
        background: transparent;
        width: 32px
    }
    /* BURGER BUTTON ANIMATION */

    .navbar_item {
        transform: translateY(100vh);
    }
    .show_nav .navbar_item {
        transform: translateY(0);
    }
    .show_nav .SlideInDown-1 {
        transition: all 1s ease-in-out;
    }
    .show_nav .SlideInDown-2 {
        transition: all 1.2s ease-in-out;
    }
    .show_nav .SlideInDown-3 {
        transition: all 1.4s ease-in-out;
    }
    .show_nav .SlideInDown-4 {
        transition: all 1.6s ease-in-out;
    }
    .show_nav .SlideInDown-5 {
        transition: all 1.8s ease-in-out;
    }
    .show_nav .SlideInDown-6 {
        transition: all 2s ease-in-out;
    }
}
